import Vue from 'vue';
import * as katex from 'katex';
export default Vue.extend({
    props: {
        formula: {
            type: String,
            required: true
        },
        block: {
            type: Boolean,
            required: true
        }
    },
    computed: {
        compiledFormula() {
            return katex.renderToString(this.formula, {
                throwOnError: false
            });
        }
    }
});
